import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import axios from 'axios';

interface User {
  id: number;
  name: string;
  email: string;
}

const UserManagement: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [error, setError] = useState<string>('');
  const [loading, setLoading] = useState<boolean>(true);

  // Função para buscar a lista de usuários da API Laravel
  const fetchUsers = async () => {
    try {
      setLoading(true); // Inicia o loading
      const response = await axios.get('/api/users'); // Endpoint da API Laravel
      setUsers(response.data); // Definindo os usuários
    } catch (err) {
      setError('Erro ao carregar a lista de usuários.');
    } finally {
      setLoading(false); // Finaliza o loading
    }
  };

  // Chamada da API ao montar o componente
  useEffect(() => {
    fetchUsers();
  }, []);

  return (
    <div className="user-management-container">
      <Sidebar />
      <div className="user-management-content">
        <h1 className="user-management-title">Gerenciamento de Usuários</h1>

        {/* Exibição de erro */}
        {error && <p className="error-message">{error}</p>}

        {/* Exibição de loading */}
        {loading ? (
          <p>Carregando usuários...</p>
        ) : (
          <ul className="user-list">
            {users.map((user) => (
              <li key={user.id} className="user-item">
                <strong>{user.name}</strong> (<em>{user.email}</em>) - ID: {user.id}
                <div className="user-actions">
                  <button
                    className="edit-button"
                    onClick={() => alert(`Editando usuário ${user.name}`)}
                  >
                    Editar
                  </button>
                  <button
                    className="delete-button"
                    onClick={() => alert(`Excluindo usuário ${user.name}`)}
                  >
                    Excluir
                  </button>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default UserManagement;
